<template>
<div class="home">
    <div class="home-header">
        <video src="../../../static/img/bg.mp4" class="glass" autoplay loop></video>
        <h2>欢迎来到Starboard的小社区</h2>
    </div>
    <home-content></home-content>
</div>
</template>

<script>
    import HomeContent from '@/components/home/HomeContent'

    export default{
        components:{HomeContent},
        data() {
            return {
            }
        }
    }
</script>

<style>
video{
    margin-top: 0;
    padding: 0;
    border: 0;
    opacity: 0.8;
    box-shadow: 0 0 10px #8d8d8d;
}
.home {
    position: relative;
    overflow: hidden;
}
.home-header {
    background-color: #1d1d1d;
    height: 800px;
    margin-bottom: 10px;
}
.glass{
    position: absolute;
    left: 0;
    z-index: 1;
    width: 100%;
}
.home h2{ 
    font-size: 40px;
    position: absolute;
    left:50%;
    margin-top:200px;
    margin-left:-250px;
    color: #fff;
    position: absolute;
    z-index: 1;
    padding: 20px 0;
    border-top:2px solid #fff;
    border-bottom:2px solid #fff;
}
</style>




